.header{
  border-radius: 5px;
  box-shadow: 0 1px 2px rgba(0,0,0,.24), 0 1px 3px rgba(0,0,0,.12);
  position: sticky !important;
  top: 0;
  z-index: 10;
  a{
    text-decoration: none;
  }
  display: flex;
  justify-content: space-between;
  // background-color: #222431;
  background-color: rgba(17, 18, 19, 0.9);
  min-height: 40px;
  margin-top: 30px;
  &__logotype{
    margin-left: 25px;
    display: flex;
    align-items: center;
    &-link{
      color: white;
      font-weight: 600;
    }
    &-img{
      width: 30px;
      height: auto;
      margin-right: 10px;
    }
  }
  &__aside{
    display: flex;
    align-items: center;
    margin-right: 25px;
  }
  &__list{
    display: flex;
    align-items: center;
    flex-direction: column;
    position: absolute;
    // background-color: #222431;
    background-color: rgba(19, 22, 29, 0.89);
    padding-top: 30px;
    overflow: hidden;
    left: -350px;
    top: 0px;
    height: 100vh;
    width: 300px;
    transition: 0.3s;
    z-index: 10;
    box-shadow: 0 1px 2px rgba(0,0,0,.24), 0 1px 3px rgba(0,0,0,.12);
    &.active{
      left: 0;
    }
    &-link{
      color: white;
      font-weight: 600;
      width: 100%;
      height: 100%;
      font-size: 20px;
    }
    &-item{
      margin-bottom: 20px;
      display: flex;
      align-items: center;
      text-align: center;
      width: 100%;
      height: 40px;
    }
  }
}
.burger{
  position: absolute;
  top: 18px;
  right: 35px;
  z-index: 11;
  width: 35px;
  height: 2px;
  &-span1{
    content: "";
      background-color: white;
      position: absolute;
      width: 35px;
      height: 3px;
      left: 0;
      bottom: -2px;
      transform: translateY(7px);
      transition: 0.3s;
    &.active{
      transform: rotate(45deg);
      transition: 0.3s;
    }
  }
  &-span2{
    content: "";
    background-color: white;
    position: absolute;
    width: 35px;
    height: 3px;
    left: 0;
    top: -1px;
    transform: translateY(-7px);
    transition: 0.3s;
    &.active{
      transform: rotate(-45deg);
      transition: 0.3s;
      top: 1px;
    }
  }
  &-span3{
    content: "";
    background-color: white;
    position: absolute;
    width: 35px;
    height: 3px;
    left: 0;
    top: 7px;
    transform: translateY(-7px);
    transition: 0.3s;
    &.active{
      transform: rotate(-45deg);
      transition: 0.3s;
      top: 0;
      height: 0px;
    }
  }
}

@media (min-width: 830px){
  .burger{
    display: none;
  }
  .header{

    a{
      text-decoration: none;
    }
    font-size: 15px;
    display: flex;
    justify-content: space-between;
    // background-color: #222431;
    background-color: rgba(17, 18, 19, 0.9);
    min-height: 40px;
    &__logotype{
      margin-left: 25px;
      display: flex;
      align-items: center;
      &-link{
        color: gray;
        &:hover{
          color: white;
        }
      }
      &-img{
        width: 30px;
        margin-right: 10px;
      }
    }
    &__aside{
      display: flex;
      align-items: center;
      margin-right: 25px;
    }
    &__list{
      display: flex;
      align-items: center;
      flex-direction: row;
      position: static;
      max-height: 50px;
      min-width: 500px;
      margin-right: 25px;
      padding-top: 0px;
      box-shadow: none;
      background-color: rgba(59, 116, 146, 0);
      &-link{
        color: rgb(161, 155, 155);
        font-size: 17px;
        &:hover{
          color: white;
        }
      }
      &-item{
        margin-right: 20px;
        margin-bottom: 0px;
        width: max-content;
        height: max-content;
        position: relative;
        &:hover{
          color: white;
        }
        &:last-child{
          margin-right: 0px;
        }
      }
    }
  }
  .header__list-link.active{
    &::before{
      content: "";
      z-index: 10;
      position: absolute;
      width: 100%;
      height: 2px;
      background-color: white;
      bottom: -5px;
      left: 0;
      transition: 0.3s;
    }
  }
}

.header__list-link.active{
  color: white;
}